<template id="">
    <div class="col-xs-8 col-xs-offset-2" id="wrapper">
      <h1>Log in to enjoy Desks!</h1>
      <form @submit.prevent="loginUser">
        <input class="col-md-5 col-xs-12" v-model="user.login" type="text" placeholder="Login">
        <input class="col-md-5 col-xs-12" v-model="user.password" type="password" placeholder="Password">
        <button class="btn btn-success col-md-2 col-xs-12" type="submit" name="button">Log in</button>
      </form>
    </div>
</template>

<script>
  export default {
    name: 'app-login',
    data() {
      return {
        user: {
          login: '',
          password: ''
        }
      }
    },
    methods: {
      loginUser () {
        firebase.auth().signInWithEmailAndPassword(this.user.login, this.user.password)
          .then(response => {
            const settings = {
                login: response.email,
                uid: response.uid,
                logComplete: true
            }
            this.$emit('addUser', settings)
          })
      }
    }
  }
</script>

<style>
#wrapper {
  font-size: 14px;
  position: absolute;
  top: 50%;
  margin-top: -150px;
  background: #fff;
  text-align: center;
  padding: 30px;
  border-radius: 8px;
}
  #wrapper h1 {
    font-size: 4em;
    height: 100px;
    line-height: 100px;
    margin: 0;
  }
  #wrapper form {
    padding: 50px 0;
    display: inline-block;
  }
  #wrapper form input,
  #wrapper form button {
    height: 30px;
    line-height: normal;
  }

  @media screen and (max-width: 992px) {
    #wrapper {
      padding: 20px 10px;
    }
    #wrapper form {
      padding: 10px 0;
    }
    #wrapper form input {
      margin-bottom: 10px;
      padding: 5px;
    }

    #wrapper h1 {
      font-size: 3em;
      line-height: normal;
    }
  }

  @media screen and (max-width: 768px) {
    #wrapper h1 {
      font-size: 2.5em;
    }
  }
  @media screen and (max-width: 450px) {
    #wrapper h1 {
      font-size: 2em;
    }
  }
</style>



